<template>
  <div class="zhuye">
       <div class="actorcontent ">
         <div class="actorItem clear" v-for="item in actor">
           <div class="actorTop">
              <img :src="item.autor" class="autor">
              <span class="autorname">
                <p class="aname"><span>{{item.name}}</span><span class="loction">深圳</span><span class="loction">web前端</span></p>
                <p class="atime">{{item.creatTime}}</p>
              </span>
             <span style="float: right"><a :href="item.href" target="_blank">原文链接</a></span>
           </div>
           <div class="actorbody">
               <div v-html="item.content" class="actorcon"></div>
               <div class="imgs">
                   <span v-for="img in item.imags" class="imgitem">
                     <img :src="img">
                   </span>
               </div>
           </div>

           <div class="actorfooter">文章摘抄于: <a :href="item.fromhref" target="_blank">{{item.fromhreftxt}}</a></div>
         </div>
       </div>
  </div>
</template>

<script>
  import photo from "@/assets/photo.png"
  import zhuye180823a from "@/assets/actorImgs/zhuye/zhuye-180823.png"
export default {
  name: 'HelloWorld',
  data () {
    return {
      actor:[{
         autor:photo,
         name:"无疑",
         creatTime:"2018-8-23",
         content:"Unicode 里面有一些不存在的汉字，称为\"幽灵文字\"。它们是怎么进入标准的？原来，1978年，日本制定本国编码标准 JIS 时有一些错误，创造出这些文字，后来 Unicode 又全部继承了 JIS。",
         imags:[zhuye180823a],
         href:"https://www.dampfkraft.com/ghost-characters.html",
         fromhreftxt:"阮一峰个人日志",
         fromhref:"http://www.ruanyifeng.com/blog/2018/08/weekly-issue-18.html"
      }, {
        autor: photo,
        name: "无疑",
        creatTime: "2018-6-14",
        content: "Nodejs 的创始人 Ryan Dahl 一共做过两次关于 JS 的公开演讲。 一次是2009年宣布 Node 项目诞生，另一次是九年后的昨天，演讲题目是《Node 的设计失误》。\n" +
        "这个演讲的内容非常火爆，基本上把 Node 全部否定了，认为 libuv 和 npm（包括 package.json）都是设计错误，怪不得 JS 圈里面没人作声。他觉得，Node 已经无药可救了，所以动手写了一个新项目 deno（这个名字是 node 的拆分，表示 node 重组）。",
        imags: [],
        href: "http://tinyclouds.org/jsconf2018.pdf",
        fromhreftxt: "阮一峰个人日志",
        fromhref: "http://www.ruanyifeng.com/blog/2018/06/weekly-issue-9.html"
      },{
         autor:photo,
         name:"无疑",
         creatTime:"2018-6-14",
         content:"北京时间6月4日晚间，微软发布公告称，将以75亿美元的微软股票收购GitHub，微软公司副总裁Nat Friedman将出任GitHub首席执行官。GitHub官方推特转发确认了这一消息。",
         imags:[],
         href:"http://tinyclouds.org/jsconf2018.pdf",
         fromhreftxt:"阮一峰个人日志",
         fromhref:"http://www.sohu.com/a/233881685_160850"
      }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.actorItem{
  width: 100%;
  background-color: #fff;
  padding: 16px;
  box-sizing: border-box;
  border-radius: 2px;
  margin-bottom: 20px;
}
  .autor{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: solid 1px #eee;
    display: inline-block;
  }
.autorname{
  display: inline-block;
  vertical-align: top;
  margin-left: 10px;
  line-height: 25px;
}
.autorname .aname{
  font-size:16px ;
  font-weight: 600;
}
.loction{
 margin-left: 15px;
  background-color: #FFB800;
  border-radius: 5px;
  font-size: 12px;
  padding:3px 5px;
  font-weight: 100;
  color: #fff;
}
.actorcon{
  font-size: 14px;
  text-indent: 30px;

}
  .imgs{
    max-height: 160px;
    overflow: hidden;
    margin: 10px 20px;
  }
  .imgitem{
    overflow: hidden;
    position: relative;
    float: left;
    width: 33.33%;
    box-sizing: border-box;
    padding: 0 10px 20px;
  }
  .imgitem img{
    width: 100%;
    float: left;
    height: auto;
    border-radius: 0;
    /*cursor: url(../images/open_new.cur), auto;*/
    border: 1px solid #f1f1f1;
  }
.actorfooter{
  margin-top: 10px;
  padding-top: 16px;
  border-top: 1px solid #f1f1f1;
  line-height:30px;
  text-align: right;
}
</style>



